@import '_var.scss';

*::-webkit-input-placeholder {
	color: #fff;
	opacity: 1;
}

*:-moz-placeholder {
	color: #fff;
	opacity: 1;
}

*::-moz-placeholder {
	color: #fff;
	opacity: 1;
}

*:-ms-input-placeholder {
	color: #fff;
	opacity: 1;
}

.forms-calldecor input::-webkit-input-placeholder {
	color: #000;
	opacity: 1;
}

.forms-calldecor input:-moz-placeholder {
	color: #000;
	opacity: 1;
}

.forms-calldecor input::-moz-placeholder {
	color: #000;
	opacity: 1;
}

.forms-calldecor input:-ms-input-placeholder {
	color: #000;
	opacity: 1;
}


*,
*:before,
*:after {
	box-sizing: border-box;
	outline: none;
	/*   padding: 0;
  margin: 0; */
}

html {
	box-sizing: border-box;
	width: 100%;
	/* height: 100%; */
	font-size: 16px;
	line-height: 1.42;
	margin: 0;
	padding: 0;
	backface-visibility: hidden;
	overflow-x: hidden;
	/* overflow-x: hidden; */
}

@import '_fonts.scss';

body {
	font-family: "OpenSansRegular", sans-serif;
	font-size: 16px;
	line-height: 1.42;
	position: relative;
	min-width: 320px;
	height: 100%;
	margin: 0;
	padding: 0;
	color: #000000;
	opacity: 1;
	backface-visibility: hidden;
	-webkit-font-smoothing: antialised;
	overflow-x: hidden;
}

