h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

a {
	outline: 0;
	text-decoration: none;
}

ul {
	margin: 0;
	padding: 0;
}

li {
	list-style-type: none;
}

.img-responsive {
	display: block;
	width: 100%;
	height: auto;
}

.hidden {
	display: none;
}

body.fix {
	overflow: hidden;
}

